'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { supabase } from '@/lib/supabase';
import AdminSidebar from '@/app/admin/components/AdminSidebar'; // adjust path if needed
import { ChevronLeft } from 'lucide-react';

type TaskType = {
  id: string;
  name: string;
};

type StoreInput = {
  name: string;
  city: string;
  address: string;
  type: 'hub' | 'garage';
  alias: string;
  password: string;
  manager_name?: string;
  manager_number?: string;
  hub_id?: string;
  taskCapacities: { [taskId: string]: number };
};

export default function CreateStorePage() {
  const [store, setStore] = useState<StoreInput>({
    name: '',
    city: '',
    address: '',
    type: 'hub',
    alias: '',
    password: '',
    manager_name: '',
    manager_number: '',
    taskCapacities: {},
  });

  const [taskTypes, setTaskTypes] = useState<TaskType[]>([]);
  const [hubs, setHubs] = useState<{ id: string; name: string }[]>([]);
  const router = useRouter();

  useEffect(() => {
    const fetchData = async () => {
      const { data: tasks } = await supabase.from('task_types').select();
      const { data: hubStores } = await supabase
        .from('store_admin')
        .select('id, name')
        .eq('type', 'hub');

      if (tasks) setTaskTypes(tasks);
      if (hubStores) setHubs(hubStores);
    };

    fetchData();
  }, []);

  const handleInputChange = (field: string, value: any) => {
    setStore((prev) => ({ ...prev, [field]: value }));
  };

  const handleCapacityChange = (taskId: string, value: number) => {
    setStore((prev) => ({
      ...prev,
      taskCapacities: { ...prev.taskCapacities, [taskId]: value },
    }));
  };

  const handleSubmit = async () => {
    const { taskCapacities, ...storeData } = store;
    const { data, error } = await supabase
      .from('store_admin')
      .insert([{ ...storeData }])
      .select()
      .single();

    if (error || !data) {
      alert('Error creating store');
      return;
    }

    const storeId = data.id;

    // Insert task capacities
    const taskPayload = Object.entries(taskCapacities).map(([taskTypeId, capacity]) => ({
      store_id: storeId,
      task_type_id: taskTypeId,
      capacity,
    }));

    await supabase.from('store_task_capacities').insert(taskPayload);

    // Tag garage to hubs
    if (store.type === 'garage' && store.hub_id) {
      await supabase.from('garage_hub_tags').insert([
        {
          garage_id: storeId,
          hub_id: store.hub_id,
        },
      ]);
    }

    router.push('/admin/dashboard/manage-store');
  };

  return (
    <div className="flex min-h-screen bg-gray-100">
      <AdminSidebar />

      <main className="flex-1 p-4 md:p-6">
        <button
          onClick={() => router.back()}
          className="flex items-center text-sm text-gray-600 mb-4"
        >
          <ChevronLeft className="w-4 h-4 mr-1" /> Back
        </button>

        <h1 className="text-xl font-semibold mb-4">Create Store</h1>

        <Tabs defaultValue="hub" onValueChange={(value) => handleInputChange('type', value as 'hub' | 'garage')}>
          <TabsList>
            <TabsTrigger value="hub">Hub</TabsTrigger>
            <TabsTrigger value="garage">Garage</TabsTrigger>
          </TabsList>

          <TabsContent value="hub">
            <StoreForm store={store} onChange={handleInputChange} taskTypes={taskTypes} onCapacityChange={handleCapacityChange} />
          </TabsContent>

          <TabsContent value="garage">
            <StoreForm store={store} onChange={handleInputChange} taskTypes={taskTypes} onCapacityChange={handleCapacityChange} hubs={hubs} />
          </TabsContent>
        </Tabs>

        <div className="mt-6">
          <Button onClick={handleSubmit}>Save Store</Button>
        </div>
      </main>
    </div>
  );
}

type StoreFormProps = {
  store: StoreInput;
  onChange: (field: string, value: any) => void;
  taskTypes: TaskType[];
  onCapacityChange: (taskId: string, value: number) => void;
  hubs?: { id: string; name: string }[];
};

function StoreForm({ store, onChange, taskTypes, onCapacityChange, hubs }: StoreFormProps) {
  return (
    <div className="grid gap-4 mt-4">
      <Input placeholder="Name" value={store.name} onChange={(e) => onChange('name', e.target.value)} />
      <Input placeholder="City" value={store.city} onChange={(e) => onChange('city', e.target.value)} />
      <Input placeholder="Address" value={store.address} onChange={(e) => onChange('address', e.target.value)} />
      <Input placeholder="Alias" value={store.alias} onChange={(e) => onChange('alias', e.target.value)} />
      <Input placeholder="Password" type="password" value={store.password} onChange={(e) => onChange('password', e.target.value)} />
      <Input placeholder="Manager Name" value={store.manager_name} onChange={(e) => onChange('manager_name', e.target.value)} />
      <Input placeholder="Manager Number" value={store.manager_number} onChange={(e) => onChange('manager_number', e.target.value)} />

      {store.type === 'garage' && hubs && (
        <select
          value={store.hub_id ?? ''}
          onChange={(e) => onChange('hub_id', e.target.value)}
          className="p-2 rounded border text-sm"
        >
          <option value="">Select Hub to Tag</option>
          {hubs.map((hub) => (
            <option key={hub.id} value={hub.id}>
              {hub.name}
            </option>
          ))}
        </select>
      )}

      <div>
        <h3 className="font-semibold mb-2">Task Capacities</h3>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
          {taskTypes.map((task) => (
            <div key={task.id}>
              <label className="text-sm">{task.name}</label>
              <Input
                type="number"
                min={0}
                value={store.taskCapacities[task.id] ?? 0}
                onChange={(e) => onCapacityChange(task.id, parseInt(e.target.value) || 0)}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
